import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

const routes: Routes = [
  {
    path: "user",
    loadChildren: () =>
      import("./app-user-components/app-user-components.module").then(
        m => m.AppUserComponentsModule
      )
  },
  {
    path: "admin",
    loadChildren: () =>
      import("./app-admin-components/app-admin-components.module").then(
        m => m.AppAdminComponentsModule
      )
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppComponentsRoutingModule {}
